import React from 'react';
import './Cards.css';
import CardItem from './CardItem';

function Cards() {
  return (
    <div className='cards'>
      <div className='cards__container'>
        <div className='cards__wrapper'>
          <h1 className='cards__title'>Experiences</h1>
          <ul className='cards__items'>
            <CardItem
              src='images/android_app_photo.png'
              text="Android App Developer"
              label='FCOM Services Co., Ltd.'
              path='/fcom'
            />
            <CardItem
              src='images/datascience.jpg'
              text="Junior Software Developer"
              label='Delta Controls Inc.'
              path='/delta'
            />
          </ul>
          <ul className='cards__items'>
          <CardItem
              src='images/mobileBanking.jpg'
              text="C#/Java Junior Developer"
              label='Fispan Inc.'
              path='/fispan'
            />
            <CardItem
              src='images/amazon.jpg'
              text="Software Developer Engineer Intern"
              label='Amazon'
              path='/amazon'
            />
            </ul>
          <h1 className='cards__title 2'>Projects</h1>
          <ul className='cards__items'>
            <CardItem
              src='images/discordbot.jpg'
              text="Text-based RPG based entirely on discord server commands. Leveling, monsters, classes, skills, shop, and more!"
              label='Discord Bot'
              path="https://discordrpg.vincentyan.repl.co/"
            />
            <CardItem
              src='images/mediatextscribe.jpg'
              text='AI-generated alt-text for images on webpages lacking alt-text'
              label='Hackathon'
              path="https://devpost.com/software/dubhacksazurefrontend"
            />
            <CardItem
              src='images/howbusy.jpg'
              text='Location based traffic analysis to visualize local grocery store traffic for nearby stores'
              label='Covid-Project'
              path="https://busyrightnow.herokuapp.com/"
            />
            </ul>
                    <ul className='cards__items'>
            <CardItem
              src='images/partnerme.jpeg'
              text='Android application to connect students with others based on similarities (a Tinder-like application for students finding partners)'
              label='Android Application'
              path='https://github.com/ovopp/PartnerMe-V2'
            />
            <CardItem
              src='images/dancingrobot.jpg'
              text='Dancing robot that coordinates music with movement. Display shows song name and routine. Controlled via Bluetooth'
              label='School-Project'
              path="https://github.com/ovopp/Bipedal-Dancing-and-Singing-Robot"
            />
            <CardItem
              src='images/ultrasonicrobot.jpg'
              text='Ultrasonic robot that measures distance using echo-location and reports to twitter if you are too close!'
              label='School-Project'
              path="https://github.com/ovopp/UltraSonic-Sensor-Robot"
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
