import React from 'react';
import '../../App.css';
import Footer from '../Footer';

function Amazon() {
  return (
    <>
      <div className="about-container">
                <div className="about">
                    <div className="column">
                        <h1>Amazon</h1>
                        <h2>Software Developer Engineer Intern</h2>
                        <h3>May 2022 - August 2022</h3>
                        <br></br>
                        <div className="about-text">
                            Currently working in Vancouver in the Personalization organization<br>
                            </br><br></br>
                            <br></br>
                            
                        </div>
                        <br></br>
                    </div>
                    <div className="column2">
                        <img
                            className="headshot"
                            alt='Amazon Logo'
                            src="images/amazon.jpg"
                        />
                    </div>
                </div>
            </div>
      <Footer />
    </>
  );
}

export default Amazon;
