import React from 'react';
import Navbar from './components/Navbar';
import './App.css';
import Home from './components/pages/Home';
import Fcom from './components/pages/Fcom';
import Fispan from './components/pages/Fispan';
import Delta from './components/pages/Delta';
import Amazon from './components/pages/Amazon';
import { Route, HashRouter, Switch } from 'react-router-dom';

function App() {
  return (
    <>
      <HashRouter>
        <Navbar />
        <Switch>
          <Route path='/'  component={Home} />
          <Route path='/fcom'  component={Fcom} />
          <Route path='/fispan'  component={Fispan} />
          <Route path='/delta'  component={Delta} />
          <Route path='/amazon'  component={Amazon} />
        </Switch>
      </HashRouter>
    </>
  );
}

export default App;
