import React from 'react';
import './About.css';

function About() {
    return (
        <>
            <div className="about-container">
                <div className="about">
                    <div className="column">
                        <h1>About Me</h1>
                        <div className="about-text">
                            I'm currently a full-stack Web Application Developer, Android Application Developer, and Software Developer working in Vancouver, British Columbia, Canada.
                            I'm a 4th year undergraduate student at the Computer Engineering program at the University of British Columbia. I additionally hold a Bachelor's of Science in Chemistry from the same university.
                            In my free time, I explore different technologies and frameworks to work on fun and interesting side-projects. At the moment I'm most interested in developing and hosting web applications that
                            have varied functionalities to hone my abilities to become a stronger Full-Stack Developer, and to have fun sharing my ideas while doing so. <br>
                            </br><br></br>
                            Throughtout the years, I have been employed in many companies and have worked on creating interactive dashboards for data science metrics, 
                            machine learning chat-bots, Android/IOS applications, setting up backend technologies for data ingestion and processing, and much more.
                            <br></br><br></br>
                            Feel free to check out my experiences and projects below!
                        </div>
                        <br></br>
                    </div>
                    <div className="column2">
                        <img
                            className="headshot"
                            alt='Headshot'
                            src="images/headshot2.jpeg"
                        />
                    </div>
                </div>
            </div>
        </>
    )
}

export default About;