import React from 'react';
import '../../App.css';
import Footer from '../Footer';

function Fcom() {
  return (
    <>
      <div className="about-container">
                <div className="about">
                    <div className="column">
                        <h1>FCOM Services Co., Ltd.</h1>
                        <h2>Application Developer</h2>
                        <h3>July 2020 - December 2020</h3>
                        <br></br>
                        <div className="about-text">
                            My first programming job, I was hired as an Application Developer to create Android, IOS, and web applications from a conceptual idea to a functioning prototype.
                            I created an image-analysis Android application using Android Studio and MongoDB as a solution for inventory/fridge management. Receipts of inventory were stored and parsed
                            to extract information regarding items and their item quantities and expiry date information.
                            A working demo was created with this feature and I was able to demo it to stakeholders in Japan.<br>
                            </br><br></br>
                            After a successful demo, I began working with other developers to develop a variety of products. I took over training and programming a
                            machine learning chat-bot that could help communicate with office workers about their stress. Using Angular and PostgreSQL, I worked on a health application which communicated with
                            a robot and monitored health information for the user and caretakers. 
                            <br></br><br></br>
                            Overall, this experience gave me confidence in collaborating in a Scrum/Agile development environment and presenting
                            work and products to stakeholders.
                            <br></br>
                            
                        </div>
                        <br></br>
                    </div>
                    <div className="column2">
                        <img
                            className="headshot"
                            alt='FCOM Services Logo'
                            src="images/fcom.jfif"
                        />
                    </div>
                </div>
            </div>
      <Footer />
    </>
  );
}

export default Fcom;
