import React from 'react';
import '../../App.css';
import Footer from '../Footer';

function Fispan() {
  return (
    <>
      <div className="about-container">
                <div className="about">
                    <div className="column">
                        <h1>Fispan</h1>
                        <h2>Java / C# Developer</h2>
                        <h3>September 2021 - December 2021</h3>
                        <br></br>
                        <div className="about-text">
                            Fispan was my first experience in the Fintech industry, and I used this opportunity to learn and develop within this 
                            field. I worked on features for a financial tool connector that bridged the tool to the Fispan services platform. 
                            One such feature was a sync health checker which visualized and notified the user whether the connection and information between the 
                            application and the platform was compromised or outdated. Additionally, I worked on Gitlab CI/CD pipelines that published new releases and notified 
                            Slack channels.<br>
                            </br><br></br>
                            
                            <br></br><br></br>
                            Fispan's company culture was extremely lively and motivated people to share new ideas and share success in work.
                            <br></br>
                            
                        </div>
                        <br></br>
                    </div>
                    <div className="column2">
                        <img
                            className="headshot"
                            alt='FCOM Services Logo'
                            src="images/fispan.png"
                        />
                    </div>
                </div>
            </div>
      <Footer />
    </>
  );
}

export default Fispan;
