import React from 'react';
import '../App.css';
import { Button } from './Button';
import './HeroSection.css';

function HeroSection() {
  return (
    <div className='hero-container'>
      <video src='/videos/Vancouver Timelapse.mp4' autoPlay loop muted />
      <h1>Software Developer</h1>
      <p>Experienced Full-Stack Web-Developer, App-Developer, Data-Visualizations, and more</p>
      <div className='hero-btns'>
        <Button
          className='btns'
          buttonStyle='btn--outline'
          buttonSize='btn--large'
        >
          RESUME
        </Button>
      </div>
      <small className='website-rights'>Vancouver Timelapse Video by: Joel Schat</small>
    </div>
  );
}

export default HeroSection;
