import React from 'react';
import './Footer.css';
import { Link } from 'react-router-dom';

function Footer() {
  return (
    <div className='footer-container'>
      <section className='social-media'>
        <div className='social-media-wrap'>
          <div className='footer-logo'>
            <Link to='/' className='social-logo'>
              Vincent Yan
            </Link>
          </div>
          <small className='website-rights'>© 2021</small>
          <div className='social-icons'>
              <a className='social-icon-link github' href="https://github.com/ovopp"><i className='fab fa-github' /></a>
              <a className='social-icon-link linkedin' href="https://www.linkedin.com/in/vincent-yan-a9789290/"><i className='fab fa-linkedin' /></a>
              <a className='social-icon-link twitch' href="https://www.twitch.tv/ovopp"><i className='fab fa-twitch' /></a>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;
