import React from 'react';
import '../../App.css';
import Footer from '../Footer';

function Delta() {
  return (
    <>
      <div className="about-container">
        <div className="about">
          <div className="column">
            <h1>Delta Controls Inc.</h1>
            <h2>Junior Software Developer</h2>
            <h3>Jan 2021 - August 2021</h3>
            <br></br>
            <div className="about-text">
              At Delta Controls, I joined the research department to develop applications and tools to utilize the data received from existing building management tools.
              I worked closely with my Data Scientist supervisor to develop visual and interactive ways to tackle alarm floods and alarm fatigue in large building complexes, data pipelines
              to ingest, store, and export clustered data, and research into a new idea for building digital twins. In addition to my projects, I hosted, developed, and maintained a research
              server for multiple demo products, using a combination of pgAdmin4 and test scripts to check the validity of the methods and data.
              <br>
              </br><br></br>
              I led the full-stack development for the features and products I worked on. Using a combination of Javascript, Zend framework, and
              HTML/CSS for the frontend and connecting everything to our research server hosting all the features and database. I created extensive
              documentation on Confluence for technical teams and management to see the progress and development considerations.
              <br></br><br></br>
              I had an amazing experience at Delta Controls, the team and people were welcoming and extremely knowledgeable in their field. I learned
              a lot from my supervisor and mentors and I'm super happy I got the opportunity to bring so many of my ideas to the company.
              <br></br>

            </div>
            <br></br>
          </div>
          <div className="column2">
            <img
              className="headshot"
              alt='delta controls'
              src="images/delta_logo.jpg"
            />
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Delta;
